// src/hooks/usePatientQueue.ts
import { useState, useEffect, useCallback } from 'react';
import axios from 'axios';

// Import necessary types and API client
import { Patient, QueueStats, NewPatientInput } from '@/lib/types';
import { api } from '@/lib/api'; // Axios instance configured for the backend URL

// Define the shape of the data returned by the hook
interface UsePatientQueueResult {
    queue: Patient[];
    stats: QueueStats | null;
    isLoading: boolean;
    isError: boolean;
    addPatient: (data: NewPatientInput) => Promise<boolean>;
    markVisited: (id: number) => Promise<boolean>;
    refetch: () => Promise<void>; // Expose refetch for manual sync if needed
}

/**
 * Custom hook to manage the global state of the patient queue and statistics.
 * It handles data fetching, error state, and mutations (add, mark visited).
 */
export const usePatientQueue = (): UsePatientQueueResult => {
    // --- State Management ---
    const [queue, setQueue] = useState<Patient[]>([]);
    const [stats, setStats] = useState<QueueStats | null>(null);
    const [isLoading, setIsLoading] = useState(true);
    const [isError, setIsError] = useState(false);

    // --- Core Fetching Logic (DRY) ---
    const fetchAllData = useCallback(async () => {
        setIsLoading(true);
        setIsError(false); // Reset error state on new attempt
        try {
            // Fetch queue and stats concurrently for performance
            const [queueRes, statsRes] = await Promise.all([
                api.get('/patients'),
                api.get('/patients/stats'),
            ]);

            // Assuming backend wraps data in { success: true, data: [...] }
            setQueue(queueRes.data.data);
            setStats(statsRes.data.data);
        } catch (error) {
            console.error("API Fetch Failed:", error);
            setIsError(true);
            setQueue([]); // Clear stale data on error
            setStats(null);
        } finally {
            setIsLoading(false);
        }
    }, []);

    // --- Lifecycle: Initial Data Load ---
    useEffect(() => {
        // Fetch data on component mount
        fetchAllData();
    }, [fetchAllData]);

    // --- Mutation Handlers (API Interaction) ---

    // 1. Add Patient (POST /patients)
    const addPatient = async (data: NewPatientInput): Promise<boolean> => {
        try {
            await api.post('/patients', data);
            
            // CRITICAL: Re-fetch all data immediately to update the queue list and statistics instantly (UX)
            await fetchAllData(); 
            return true;
        } catch (error) {
            // Check for Axios error to extract backend message
            if (axios.isAxiosError(error) && error.response?.data?.message) {
                // Throw the detailed backend error message (e.g., Zod validation errors)
                throw new Error(error.response.data.message);
            }
            throw new Error("Failed to add patient due to a network or server error.");
        }
    };

    // 2. Mark as Visited (PUT /patients/{id}/visit)
    const markVisited = async (id: number): Promise<boolean> => {
        try {
            await api.put(`/patients/${id}/visit`);

            // CRITICAL: Re-fetch all data immediately to remove the patient from the list and update statistics
            await fetchAllData();
            return true;
        } catch (error) {
            if (axios.isAxiosError(error) && error.response?.data?.message) {
                throw new Error(error.response.data.message);
            }
            throw new Error(`Failed to mark patient ID ${id} as visited.`);
        }
    };

    // --- Hook Return ---
    return {
        queue,
        stats,
        isLoading,
        isError,
        addPatient,
        markVisited,
        refetch: fetchAllData,
    };
};