// src/components/forms/PatientEntryForm.tsx
'use client';

import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { toast } from 'sonner'; // 1. USE SONNER FOR NOTIFICATIONS

import { AddPatientSchema, AddPatientFormValues } from '@/lib/validation';

// Import the central state hook and reusable field component
import { usePatientQueue } from '@/hooks/usePatientQueue';
import { PatientFields } from './PatientFields'; 

// Import all required shadcn components
import { Form } from '@/components/ui/form';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

//  This component contains the application-specific logic.
export function PatientEntryForm() {
    const { addPatient } = usePatientQueue();
    const [isSubmitting, setIsSubmitting] = useState(false);

    // 1. Setup Form Hook with Zod Resolver
    const form = useForm<AddPatientFormValues>({
        resolver: zodResolver(AddPatientSchema),
        defaultValues: {
            name: '',
            problem: '',
            priority: 'Normal', // Default to Normal
        },
    });

    // 2. Submission Handler
    async function onSubmit(values: AddPatientFormValues) {
        setIsSubmitting(true);
        try {
            await addPatient(values);
            
            // 2. USE SONNER'S SUCCESS FUNCTION
            toast.success('Patient Added!', {
                description: `${values.name} successfully added to the waiting queue.`,
                duration: 3000,
            });
            
            // Reset form fields, keeping the last selected priority for efficiency
            form.reset({ 
                name: '', 
                problem: '', 
                priority: form.getValues('priority')
            }); 
        } catch (error) {
            // Error handling from the backend/hook
            const errorMessage = error instanceof Error ? error.message : "An unknown error occurred.";
            
            // 3. USE SONNER'S ERROR FUNCTION
            toast.error('Submission Failed', {
                description: errorMessage,
                duration: 5000,
            });
        } finally {
            setIsSubmitting(false);
        }
    }

    // 3. Render Form
    return (
        <Card className="w-full">
            <CardHeader>
                <CardTitle>Add Patient to Queue</CardTitle>
            </CardHeader>
            <CardContent>
                <Form {...form}>
                    <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
                        {/* REUSABILITY: Delegates rendering to PatientFields component */}
                        <PatientFields control={form.control} />
                        
                        <Button type="submit" className="w-full" disabled={isSubmitting}>
                            {isSubmitting ? 'Adding...' : 'Add to Queue'}
                        </Button>
                    </form>
                </Form>
            </CardContent>
        </Card>
    );
}