// src/lib/api.ts
import axios from 'axios';

// Define the base URL for the backend API.
// NOTE: We assume the Express.js server runs on port 3000 as configured earlier.
const BASE_URL = 'http://localhost:3030';

/**
 * Custom Axios instance configured for the Patient Queue Backend API.
 * This centralizes configuration, ensuring all API calls are consistent.
 */
export const api = axios.create({
    baseURL: BASE_URL,
    timeout: 10000, // Timeout after 10 seconds
    headers: {
        'Content-Type': 'application/json',
    },
});

// Optional: Add a response interceptor for global error handling (e.g., logging)
api.interceptors.response.use(
    (response) => {
        // Return only the data portion from the backend response for convenience
        // This makes 'api.get(...)' return { success: true, data: [...] } directly
        return response;
    },
    (error) => {
        // Log errors here globally before they reach the hook/component
        console.error("API Error Intercepted:", error.response?.data || error.message);
        
        // Return a rejected Promise so the error can be handled by the caller (usePatientQueue)
        return Promise.reject(error);
    }
);